import Row from 'react-bootstrap/Row'
import Container from 'react-bootstrap/Container';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import React, { useState, useEffect, useRef } from 'react';
import ReactDOM from 'react-dom';
import './App.css'
import { Form, Image, Modal } from 'react-bootstrap';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

const App = () => {
  const MySwal = withReactContent(Swal)
    const questionAnswer = [
        {
          question: "",
          answer: [
            ["H, He", true],
            ["HO, H", false],
            ["C, Fe", false],
            ["Ar, Ne", false],
          ],
          isExtra: false,
          knowledge: `    `,
          image: ["data:image/png;base64,"]
        },
        {
          question: "",
          answer: [
            ["", false],
            ["", true],
          ],
          knowledge: ` 100,000 10,000    14,000 `,
          image: [],
          isExtra: false
        },
        {
          question: "",
          answer: [
            ["", false],
            ["", false],
            ["", false],
            ["", true],
          ],
          knowledge: ` 4     `,
          image: ["data:image/png;base64,"]
        },
        {
          question: "",
          answer: [
            ["", false],
            ["", true],
            ["", false],
            ["", false]
          ],
          isExtra: false,
          knowledge: `    `,
          image: ["data:image/png;base64,"],
        },
        {
          question: "",
          isExtra: true,
          answer: [
            ["data:image/png;base64,", false],
            ["data:image/png;base64,", false],
            ["data:image/png;base64,", true],
            ["data:image/png;base64,", false],
          ],
          knowledge: `     `,
          image: []
        },
        {
          question: "   ",
          answer: [
            ["", true ,"data:image/png;base64,"],
            ["", false, "data:image/png;base64,"],
            ["", false, "data:image/png;base64,"],
            ["", false, "data:image/png;base64,"]
          ],
          isExtra: false,
          knowledge: `   H  He `,
          image: [], 
        },
        {
          question: " ",
          answer: [
            ["5    ", false],
            ["3    ", false],
            ["4      ", true],
            ["2   ", false],
          ],
          knowledge: ` `,
          image: [],
          isExtra: false
        },
        {
          question: "",
          answer: [
            ["    ", false],
            ["  ", false],
            ["", false],
            ["  ", true],
          ],
          knowledge: `-22    `,
          image: [],
          isExtra: false
        },
        {
          question: " ",
          answer: [
            [" ", false],
            ["", true],
          ],
          knowledge: `  `,
          image: [],
          isExtra: false
        },
        {
          question: "  ",
          answer: [
            ["  ", false],
            ["", true],
          ],
          knowledge: `          `,
          image: ["data:image/png;base64,"],
          isExtra: false
        },
    ];
    const [username, setUsername] = useState("");
    const usernameref = useRef(null);
    const [questionData, setquestionData] = useState([]);
    const [gameState, setgameState] = useState(0);    
    const [scoreState, setScoreState] = useState(0); 
    if(questionData.length === 0){
      for (let i = 0; i < questionAnswer.length; i++) {
        let randomIndex = Math.floor(Math.random() * questionAnswer.length);
        [questionAnswer[i], questionAnswer[randomIndex]] = [questionAnswer[randomIndex], questionAnswer[i]];
      } 
      setquestionData(questionAnswer)
    }
    useEffect(() => {
      
      if(!localStorage.getItem("username")){
        const root = document.querySelector('.app')
        if(root){
          const element = <div className="modal show "style={{ display: 'block', position: 'initial' }}>
              <Modal.Dialog>
                    <Modal.Header closeButton>
                      <Modal.Title></Modal.Title>
                    </Modal.Header>
                  <Modal.Body>
                    <Form>
                      <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label></Form.Label>
                        <Form.Control type="text" placeholder="" onChange={(e) => setUsername(e.target.value)}  ref={usernameref} id="username-input"/>
                      </Form.Group>
                    </Form>
                  </Modal.Body>
                  <Modal.Footer>
                    <Button variant="primary" onClick={() => {
                      localStorage.setItem("username", username)
                      window.location.reload()
                    }}></Button>
                  </Modal.Footer>
              </Modal.Dialog>
          </div>
          ReactDOM.render(element, root)
        }
      }else{
        setUsername(localStorage.getItem("username"))
        
      }
     
    }, [username, setUsername])
    let counter = 0;
    if(!questionData.length){
      return <p> Loading...</p>
    }
    return (
      <div className="app">
        { ((questionData.length === (gameState + 1) ) === false) ? 
         <Container>
            <div className="game-content">
              <Card>
                <Card.Body>
                    <div id="game-header">
                      <h2> : {questionData[gameState].question}</h2>
                      { questionData[gameState].image.length > 0 ? <>
                      <img src={questionData[gameState].image[0]} className="img-responsive center-block center" alt={questionData[gameState].knowledge}/> 
                      </> : ""}
                    </div>
                    <div id="game-answer">
                      { questionData[gameState].image.length > 0 ? <br/> : "" }
                       <Row>
                          {
                            (questionData[gameState].answer[0].length < 3 && !questionData[gameState].isExtra) ?
                              questionData[gameState].answer.map(e => (
                              <Col key={e.id}>
                                <div className="game-answer-button">
                                  <Button variant="primary" size="lg" data-info={e[0]} onClick={(el) => {
                                    const info = el.target.getAttribute('data-info');
                                    const result = questionData[gameState].answer.find(el => el[0] === info)
                                    if(result[1]){
                                      MySwal.fire({
                                        position: 'top-end',
                                        timer: 2000,
                                        icon: 'info',
                                        titleText: questionData[gameState].knowledge,
                                        toast: true,
                                        timerProgressBar: true
                                      })
                                      setgameState(gameState + 1);
                                      const allHide = document.querySelectorAll('.hide')
                                      if(allHide.length > 0){
                                        for (let i = 0; i < allHide.length; i++) {
                                          const element = allHide[i];
                                          if(element.classList.contains('hide')){
                                            element.classList.remove('hide');
                                          }
                                        }
                                      }else{
                                        setScoreState(scoreState + 1)
                                      }
                                    }else{
                                      MySwal.fire({
                                        position: 'top-end',
                                        timer: 2000,
                                        icon: 'info',
                                        titleText: "",
                                        toast: true,
                                        timerProgressBar: true
                                      })
                                      el.currentTarget.classList.add('hide')
                                      MySwal.fire({
                                        position: 'top-end',
                                        timer: 2000,
                                        icon: 'error',
                                        titleText: " ",
                                        toast: true,
                                        timerProgressBar: true
                                      })
                                    }
                                    
                                  }}>{e[0]}</Button>
                                </div>
                                {counter % 2 === 0 ? <br /> : null}
                              </Col>)) :  (questionData[gameState].isExtra) ? questionData[gameState].answer.map(e => (
                              <Col key={e.id}>
                                <div className="game-answer-button">
                                  <img src={e[0]} className="img-responsive center-block center click-image" alt={e[0]} data-info={e[0]} width="372px" height="256" onClick={(el) => {
                                    const info = el.target.getAttribute('data-info');
                                    const result = questionData[gameState].answer.find(el => el[0] === info)
                                    setgameState(gameState + 1);
                                    if(result[1]){
                                      setScoreState(scoreState + 1)
                                    }
                                    MySwal.fire({
                                      position: 'top-end',
                                      timer: 2000,
                                      icon: 'info',
                                      titleText: questionData[gameState].knowledge,
                                      toast: true,
                                      timerProgressBar: true
                                    })
                                  }}/> 
                                </div>
                                {counter % 2 === 0 ? <br /> : null}
                              </Col>)) : questionData[gameState].answer.map(e => (
                              <Col key={e.id}>
                                <div className="game-answer-button">
                                  <img src={e[2]} className="img-responsive center-block center click-image" alt={e[0]} data-info={e[0]} width="372px" height="256" onClick={(el) => {
                                    const info = el.target.getAttribute('data-info');
                                    const result = questionData[gameState].answer.find(el => el[0] === info)
                                    setgameState(gameState + 1);
                                    if(result[1]){
                                      setScoreState(scoreState + 1)
                                    }
                                    MySwal.fire({
                                      position: 'top-end',
                                      timer: 2000,
                                      icon: 'info',
                                      titleText: questionData[gameState].knowledge,
                                      toast: true,
                                      timerProgressBar: true
                                    })
                                  }}/> 
                                </div>
                                {counter % 2 === 0 ? <br /> : null}
                              </Col>))
                            }
                       </Row>
                    </div>
                </Card.Body>
              </Card>
            </div>
         </Container> : <div className="modal show "style={{ display: 'block', position: 'initial' }}>
                <Modal.Dialog>
                      <Modal.Header closeButton>
                        <Modal.Title> {username}</Modal.Title>
                      </Modal.Header>
                    <Modal.Body>
                        <h2> {scoreState} / {gameState + 1}</h2>
                        <p> { (scoreState < (gameState / 2)) ? "" : ""}</p>
                    </Modal.Body>
                    <Modal.Footer>
                      <Button variant="primary" onClick={() => {
                        window.location.reload()
                      }}></Button>
                    </Modal.Footer>
                </Modal.Dialog>
            </div> }
      </div>
    );
}

export default App;
